import { useState } from "react";
import numeral from "numeral";
import toast from "react-hot-toast";
import PropTypes from "prop-types";
import * as helper from "../../common/helper";
import { Link as RouterLink } from "react-router-dom";
import {
  Box,
  Button,
  Card,
  CardHeader,
  Divider,
  InputAdornment,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Link,
  TextField,
} from "@material-ui/core";
import SearchIcon from "../../../icons/Search";
import PaymentPopup from "./paymentPopUp";
import Scrollbar from "../../Scrollbar";
import { PAGES } from "../../../constants";
import { useDispatch } from "react-redux";
import GHS from "../../../icons/GHS";
import { Sync } from "@material-ui/icons";
import { getAllPayments } from "../../../api/PaymentsApi";
import { PaymentActions } from "../../../slices/Payments";

const statusOptions = [
  {
    label: "All",
    value: "all",
  },
  {
    label: "Success",
    value: "success",
  },
  {
    label: "Pending",
    value: "pending",
  }
];

const applyFilters = (invoices, query, filters) =>
  invoices.filter((invoice) => {
    let matches = true;

    if (query) {
      const properties = ["status"];
      let containsQuery = false;

      properties.forEach((property) => {
        let value = helper.get_object_value(invoice, property, "").toString();
        if (value && value.toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    if (filters.status && invoice.status !== filters.status) {
      matches = false;
    }

    return matches;
  });

const applyPagination = (invoices, page, limit) =>
  invoices.slice(page * limit, page * limit + limit);

const InvoiceListTable = (props) => {
  const dispatch = useDispatch();
  const { invoices, ...other } = props;
  const [page, setPage] = useState(0);
  const [rideData, setRideData] = useState();
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState("");
  const [openPaymentPopUp, setOpenPaymentPopUp] = useState(false);
  const [filters, setFilters] = useState({ status: null });

  const handleQueryChange = (event) => {
    setQuery(event.target.value);
  };

  const handleStatusChange = (event) => {
    let value = null;

    if (event.target.value !== "all") {
      value = event.target.value;
    }

    setFilters((prevFilters) => ({
      ...prevFilters,
      status: value,
    }));
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value, 10));
  };

  const handlePaymentPopup = (data) => {
    setRideData(data);
    setOpenPaymentPopUp(true);
  };
  const handleCancelPaymentPopUp = () => {
    setOpenPaymentPopUp(false);
  };

  const getPayments = async () => {
    try {
      const response = await getAllPayments();
      dispatch(PaymentActions.getPayments(response))
    } catch (error) {
      toast.error("Something went wrong");
    };
  };

  const handlePaymentSync = () => {
    getPayments();
  };

  const filteredInvoices = applyFilters(invoices, query, filters);
  const paginatedInvoices = applyPagination(filteredInvoices, page, limit);

  return (
    <>
      <Card {...other}>
        <CardHeader title="Payments" />
        <Divider />
        <Box
          sx={{
            alignItems: "center",
            display: "flex",
            flexWrap: "wrap",
            m: -1,
            p: 2,
          }}
        >
          <Box
            sx={{
              m: 1,
              maxWidth: "100%",
              width: 400,
            }}
          >
            <TextField
              fullWidth
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon fontSize="small" />
                  </InputAdornment>
                ),
              }}
              onChange={handleQueryChange}
              placeholder="Search Payment by Admin"
              value={query}
              variant="outlined"
            />
          </Box>

          <Box
            sx={{
              m: 1,
              maxWidth: "100%",
              width: 240,
            }}
          >
            <TextField
              fullWidth
              label="Status"
              name="status"
              onChange={handleStatusChange}
              select
              SelectProps={{ native: true }}
              value={filters.status || "all"}
              variant="outlined"
            >
              {statusOptions.map((statusOption) => (
                <option key={statusOption.value} value={statusOption.value}>
                  {statusOption.label}
                </option>
              ))}
            </TextField>
          </Box>
          <Box style={{marginLeft : 'auto'}}>
            <Button onClick={handlePaymentSync}>
              <Sync />
            </Button>
          </Box>
        </Box>
        <Scrollbar>
          <Box>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Payment Id</TableCell>
                  <TableCell>Ride Request Ids</TableCell>
                  <TableCell>Tip Request Ids</TableCell>
                  <TableCell>Paid To</TableCell>
                  <TableCell>Total Ride Amount</TableCell>
                  <TableCell>Total Tip Amount</TableCell>
                  <TableCell>Amount To Be Paid</TableCell>
                  <TableCell>Amount Paid</TableCell>
                  <TableCell>Paid At</TableCell>
                  <TableCell>Payment</TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {paginatedInvoices.length === 0 ? (<TableCell align={'center'} colSpan={'100%'}>No Records Found</TableCell>)
                  : paginatedInvoices.map((payments) => {
                    return (
                      <TableRow key={payments.rideRequestIds}>
                        {((payments.id) === undefined) || ((payments.id) === null)
                          ? <TableCell>-</TableCell>
                          : <TableCell>{`${payments?.id}`}</TableCell>
                        }
                        <TableCell>
                          <Link
                            color="textPrimary"
                            // component={RouterLink}
                            // to={`/${PAGES.PAYMENT_DETAILS.replace(
                            //   ":id",
                            //   payments.rideRequestIds
                            // )}`}
                            underline="none"
                            variant="subtitle2" >
                            {payments?.rideRequestIds
                              ? `${(payments?.rideRequestIds).split(',').join(', ')}`
                              : ''
                            }
                          </Link>
                        </TableCell>
                        <TableCell>
                        <Link
                            color="textPrimary"
                            // component={RouterLink}
                            // to={`/${PAGES.PAYMENT_DETAILS.replace(
                            //   ":id",
                            //   payments.rideRequestIds
                            // )}`}
                            underline="none"
                            variant="subtitle2" >
                            {payments?.tipRideRequestIds
                              ? `${(payments?.tipRideRequestIds).split(',').join(', ')}`
                              : ''}

                          </Link>
                        </TableCell>
                        <TableCell>
                          <Link
                            color="inherit"
                            component={RouterLink}
                            to={`/${PAGES.DRIVER_DETAILS.replace(
                              ":id",
                              payments.paidTo
                            )}`}
                          >
                            {payments.paidTo}
                          </Link>
                        </TableCell>
                        <TableCell>
                        <GHS className='small' />
                          {`${payments?.totalRideAmount}`}
                        </TableCell>
                        <TableCell>
                        <GHS className='small' /> 
                          {`${payments?.totalTipAmount}`}
                        </TableCell>
                        <TableCell><GHS className='small' />{numeral(payments?.amountToBePaid).format(`${payments.amountToBePaid}0,0.00`)}</TableCell>
                        <TableCell><GHS className='small' />{numeral(payments?.amountPaid).format(`${payments.amountPaid}0,0.00`)}</TableCell>
                        {(payments.paidAt) === null
                          ? <TableCell>-</TableCell>
                          : <TableCell>{`${new Date(payments?.paidAt).toLocaleDateString('en-US')}`}</TableCell>
                        }
                        {(payments.status) === 'pending'
                          ? <TableCell><Button onClick={() => { handlePaymentPopup(payments) }}>{(payments.status)}</Button></TableCell>
                          : <TableCell><Button disabled>{(payments.status)}</Button></TableCell>
                        }
                      </TableRow>
                    )
                  })
                }
              </TableBody>
            </Table>
          </Box>
        </Scrollbar>
        <TablePagination
          component="div"
          count={filteredInvoices.length}
          onPageChange={handlePageChange}
          onRowsPerPageChange={handleLimitChange}
          page={page}
          rowsPerPage={limit}
          rowsPerPageOptions={[5, 10, 25]}
        />
      </Card>
      <PaymentPopup
        open={openPaymentPopUp}
        handleClose={handleCancelPaymentPopUp}
        propRideData={rideData}
      />
    </>
  );
};

InvoiceListTable.propTypes = {
  invoices: PropTypes.array.isRequired,
};

export default InvoiceListTable;
