import { Dialog, Container, Box, Typography, Button, TextField } from "@material-ui/core";
import { reset } from "numeral";
import { useEffect, useState } from "react";
import toast from "react-hot-toast";
import { useDispatch, useSelector } from "react-redux";
import { initiatePayment } from "../../../api/PaymentsApi";
import { PaymentActions } from "../../../slices/Payments";

const PaymentPopup = (props) => {
    const { handleClose, open, primaryColor, propRideData } = props;
    const reduxDriver = useSelector((state) => state.UData.drivers);
    const [amount, setAmount] = useState();
    const dispatch = useDispatch();
    
    useEffect(() => {
        setAmount(propRideData?.amountToBePaid)
    }, [propRideData])

    const handleOnChange = (event) => {
        if (event.target.value) { setAmount(event.target.value) }
    };

    const PaymentToDriver = async () => {
        try {
            await initiatePayment({
                paidTo: propRideData?.paidTo,
                amountPaid: parseFloat(amount),
                amountToBePaid: propRideData?.amountToBePaid,
                rideRequestIds: propRideData?.rideRequestIds,
                tipRideRequestIds: propRideData?.tipRideRequestIds
            });
            dispatch(
                PaymentActions.setPaymentStatus({
                    paidTo: propRideData?.paidTo,
                    amountPaid: propRideData?.amountPaid,
                    amountToBePaid: propRideData?.amountToBePaid,
                    rideRequestIds: propRideData?.rideRequestIds,
                    tipRideRequestIds:propRideData?.tipRideRequestIds,
                    paidAt: new Date().toLocaleDateString('en-US'),
                    status: "initiated"
                }))
            toast.success("Payment initiated!");
        } catch (error) {
            console.error(error);
            toast.error(error.response.data.message);
        }
    };

    const handleConfirm = async () => {
        if ((propRideData?.amountToBePaid) < amount) {
            toast.error("Amount cannot be more than total amount to be paid!");
            setAmount(reset);
        } 
        else if((propRideData?.totalTipAmount) > Number(amount)){
            console.log(propRideData, amount)
            toast.error("Amount cannot be lesser than total tip amount!");
            setAmount(reset);
        }
        else {
            await PaymentToDriver();
            setAmount(reset);
            handleClose();
        }
    };
    return (
        <Dialog
            open={open}
            onClose={() => handleClose()}
            aria-labelledby="alert-dialog-title"
            aria-describedby="alert-dialog-description"
            sx={{ zIndex: 1320 }}>
            <Container
                maxWidth="sm"
                sx={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "space-evenly",
                    height: "100%",
                    width: "100%",
                    padding: "20px"
                }}>
                <Box
                    sx={{ display: "flex", flexDirection: "column" }}>
                    <Typography color="textPrimary" variant="h5"> {"Confirm payment"} </Typography>
                </Box>
                <Box sx={{marginTop:'10px'}}>
                    <TextField
                        id="driverId"
                        label="Driver Name"
                        types="string"
                        fullWidth
                        disabled
                        variant="standard"
                        defaultValue={reduxDriver[propRideData?.paidTo]?.firstName + ' ' + reduxDriver[propRideData?.paidTo]?.lastName}
                    >
                    </TextField>
                </Box>
                <Box sx={{marginTop:'10px'}}>
                    <TextField
                        id="amountToBePaid"
                        label="Amount To Be Paid"
                        types="number"
                        fullWidth
                        disabled
                        variant="standard"
                        defaultValue={propRideData?.amountToBePaid}
                    >
                    </TextField>
                </Box>
                <Box sx={{marginTop:'10px'}}>
                    <TextField
                        id="amount"
                        label="Enter Amount"
                        types="number"
                        fullWidth
                        variant="standard"
                        onChange={handleOnChange}
                        defaultValue={amount}
                    >
                    </TextField>
                </Box>
                <Box
                    sx={{ display: "flex", justifyContent: "flex-end", marginTop:'10px'}}>
                    <Button
                        variant="outlined"
                        onClick={() => handleClose()}
                        color="error"
                        sx={{ ":hover": { color: "red" } }}>
                        {"Cancel"}
                    </Button>
                    <Button
                        sx={{
                            backgroundColor: `${primaryColor ? "" : "primary.main"}`,
                            "&:hover": { color: "white", backgroundColor: `${primaryColor ? "" : "primary.main"}` }, ml: 2,
                        }}
                        variant="contained"
                        onClick={handleConfirm}
                        color="primary"
                        autoFocus>
                        {"Pay"}
                    </Button>
                </Box>
            </Container>
        </Dialog>
    );
};

export default PaymentPopup;